@font-face {
  font-family: 'Roboto';
  src: url("../public/assets/Roboto.ttf") format('truetype');
  font-weight: 400;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto';
}

html, body {
  width: 100vw;
  height: 100vh;
  font-family: Arial, sans-serif;
  background-color: #1a1a1a;
  color: #ffffff;
}