.wrapper{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    user-select: none;

}
.header {
    margin-top: 20px;
    font-size: 4em;
}
.statusfalse{
    font-size: 0.9em;
    margin-bottom: 0.8em;
    color: red;
}
.statustrue{
    font-size: 0.9em;
    margin-bottom: 0.8em;
    color: green;
}

.button{
    display: block;
    padding: 15px 25px;
    font-size: 18px;
    text-align: center;
    border: none;
    border-radius: 5px;
    background-color: #333333;
    color: #ffffff;
    cursor: pointer;
    transition: background-color 0.3s;
}

.button:hover {
    background-color: #555555;
}

.center{
    align-items: center;
    text-align: center;
    justify-content: center;
}